import React, {useState} from 'react';
import './confirmation.css';
import axios from 'axios';

const Confirmation = React.memo(({fav,dataNominators,next,back}) => {

	const findIdNom = (dataNominators, nominator) => {
		for(let i = 0; i < dataNominators.length ; i++) {
			if(dataNominators[i].name === nominator) return dataNominators[i]._id;
		}
	}
	
	const [state, setState] = useState('confirmation__entering');

	setTimeout(() => {
		if( state === 'confirmation__entering') setState('confirmation');
	},200);


	const buttonClicked = (e, yesOrNo) => {
		e.target.classList.add('confirmation-button-clicked');
		setTimeout(() => {
			e.target.classList.remove('confirmation-button-clicked');
		}, 100);
		setTimeout(() => {
			e.target.classList.add('confirmation-button-clicked');
		}, 200);
		setTimeout(() => {
			e.target.classList.remove('confirmation-button-clicked');
			setState('confirmation__exiting');
		}, 300);
		setTimeout(() => {
			if(yesOrNo) {
				axios.post(`https://form-staff.cyclic.app/addVoter/${findIdNom(dataNominators, fav)}`)
				.catch(err => console.log(err))
				next()
			} else {
				back()
			}
		},800)
	}

	return (
		<div className={`${state}`}>
			<h3 className="confirmation__question">{`staff favorit kamu adalah ${fav === 'Ali' ? 'bang' : 'kak'} ${fav}?`}</h3>
		   	<div className="confirmation__button-wrapper">
		   		<button className="confirmation__button" onClick={(e) => buttonClicked(e, true)} >Iya</button>
		   		<button className="confirmation__button" onClick={(e) => buttonClicked(e, false)} >Ubah pilihan</button>
		   	</div>
		</div>
	)
});

export default Confirmation;